import {withRouter} from 'next/router'
import styled from 'styled-components';
import Link from 'next/link';
import Layout from '../components/MyLayout.js'

const StyledContent = styled.div`
  font-family: "Silkscreen", sans-serif;
  background-color: white;
  nav{
    display: flex;
    justify-content: space-between;
    padding: 5px;
    margin: 10px 0 0 0;
    background-color: red;
    color: white;
    a{
      color: white;
      text-decoration: none;
    }
  }
  h1 {
    padding: 10px;
    background-color: white;
    color: black;
    font-size: 3rem;
  }
`

class PokedexEntry extends React.Component {

  static async getInitialProps({ query }) {
    let data = {};
    try {
      data.pokemon = await(await fetch(`https://pokeapi.co/api/v2/pokemon/${query.id}`)).json();
      data.species = await(await fetch(`https://pokeapi.co/api/v2/pokemon-species/${query.id}`)).json();
    } catch(err) {

    }
    return data;
  }

  render() {
    const {pokemon, species} = this.props;
    console.log(this.props);
    if(!pokemon.name) {
      return (
        <h1>404 Page not found</h1>
      )
    } else {
      return (
        <Layout>
          <StyledContent>
            <nav>
              {species.id>1 ? <Link href={`/pokemon?id=${pokemon.id-1}`}><a>Prev</a></Link> : null}
              <Link href="/"><a>Home</a></Link>
              {species.id<718 ? <Link href={`/pokemon?id=${pokemon.id+1}`}><a>Next</a></Link> : null}
            </nav>
            <h1>{species.name} - {species.names[1].name}</h1>
            <img src={pokemon.sprites.front_default} />
            <p>{species.flavor_text_entries[1].flavor_text}</p>
          </StyledContent>
        </Layout>
      )
    }
  }
}

export default PokedexEntry;
